body {
  background-attachment: fixed;
  background-color: #000000;
  background-image: url("https://www.r5.ro/2020/galaxy.jpg");
  background-position: center center;
  background-repeat: repeat;
  background-size: contain;
  margin: 0;
  padding: 0;
}

* {
  color: #ffffff;
  font-family: 'Heebo', sans-serif;
  font-weight: 200;
  letter-spacing: 0.02em;
}

.container {
  display: flex;
  flex-direction: column;
}

.row {
  align-content: center;
  align-items: center;
  align-self: center;
  background: rgba(0, 0, 0, 0.6);
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;
  max-width: 1200px;
  padding: 0 2em;
  width: 100%;
}

@media (min-width: 769px) {
  .row {
    flex-direction: row;
  }
}

.row.section-head {
  background: transparent;
  text-align: center;
}

.row.section-head h2 {
  align-content: center;
  align-items: center;
  display: flex;
  font-size: 60px;
  letter-spacing: 0.1em;
  margin: 0;
  padding: 1.2em 0;
  text-shadow: 0 0 8px #ffffff;
  width: 100%;
}

.row.section-head h2 > * {
  display: block;
}

.row.section-head h2 > amp-img {
  margin: -150px auto;
  width: 300px;
}

@media (max-width: 767px) {
  .row.section-head h2 > amp-img {
    display: none;
  }
}

.row.section-head h2 > amp-img:last-of-type {
  transform: scaleX(-1);
}

.row.section-head h2 > span {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
  margin: auto -300px;
}

@media (max-width: 1192px) {
  .row.section-head h2 > span {
    margin: auto -18vw;
  }
}

@media (max-width: 767px) {
  .row.section-head h2 > amp-img {
    display: none;
  }
  .row.section-head h2 > span {
    margin: auto;
  }
}

@media (max-width: 1200px) {
  .row {
    max-width: calc(100% - 64px);
  }
}

.col {
  display: inline-block;
  flex: 0 1 auto;
}

@media (min-width: 769px) {
  .col {
    flex: 1;
  }
}

.col > div {
  display: flex;
  flex-direction: column;
  margin-left: 1em;
  margin-right: 1em;
}

@media (min-width: 769px) {
  .col.double {
    flex: 2;
  }
}

@media (min-width: 769px) {
  .col.triple {
    flex: 3;
  }
}

@media (min-width: 769px) {
  .col.x4 {
    flex: 4;
  }
}

@media (min-width: 769px) {
  .col.x5 {
    flex: 5;
  }
}

hr {
  background: linear-gradient(to right, transparent 0%, #ffffff 50%, transparent 100%);
  border: 0;
  display: block;
  height: 1px;
  margin: 1em 0;
  opacity: 0.8;
  padding: 0;
}

p {
  margin: 0;
  margin-bottom: 1em;
  padding: 0;
}

main {
  margin-bottom: 2.2em;
}

amp-img {
  background-color: transparent;
  pointer-events: none;
}

a.cta {
  background-color: rgba(255, 255, 255, 0.1);
  display: inline-block;
  font-weight: 400;
  padding: 0.3em 0.8em 0.7em 0.5em;
  text-decoration: none;
  transition: background-color 1s ease, color 1s ease;
  white-space: nowrap;
}

@media (max-width: 768px) {
  a.cta {
    text-align: center;
    width: calc(100% - 1.3em);
  }
}

@media (max-width: 374px) {
  a.cta {
    letter-spacing: -0.005em;
  }
}

a.cta span {
  display: inline-block;
  margin-left: 0.1em;
  margin-right: 0.3em;
  opacity: 0.5;
  position: relative;
  top: 0.27em;
  transition: background-color 1s ease, color 1s ease;
}

a.cta:hover {
  background-color: rgba(255, 255, 255, 0.9);
  color: #233549;
  transition: background-color 0.1s ease, color 0.1s ease;
}

a.cta:hover span {
  color: #233549;
  transition: background-color 0.1s ease, color 0.1s ease;
}

a.cta.bordered {
  border: 1px solid rgba(255, 255, 255, 0.5);
}

a.img {
  border-radius: 5px;
  display: block;
  transition: background-color 1s ease;
}

a.img > amp-img {
  transition: opacity 1s ease;
}

a.img:hover {
  background-color: #ffffff;
  transition: background-color 0.1s ease;
}

a.img:hover > amp-img {
  opacity: 0.8;
  transition: opacity 0.1s ease;
}

header .row {
  background: linear-gradient(to top, rgba(35, 53, 73, 0.9) 0%, transparent 100%);
}

header .text {
  padding-right: 3em;
}

header .portrait {
  align-content: center;
  align-items: center;
  flex-direction: row;
  padding-top: 1.5em;
}

header .portrait a {
  border-radius: 50%;
  display: block;
  height: 400px;
  margin: auto;
  max-height: 60vw;
  max-width: 60vw;
  overflow: hidden;
  width: 400px;
}

header .portrait amp-img {
  display: block;
  max-width: 60vw;
  max-height: 60vw;
  width: 100%;
  height: 100%;
}

header nav ul {
  list-style-type: none;
  padding: 0;
  width: 100%;
}

@media (max-width: 768px) {
  header nav ul {
    margin-left: -10px;
  }
}

header nav ul li {
  margin-bottom: 0.2em;
  max-width: 255px;
  text-align: left;
}

@media (max-width: 768px) {
  header nav ul li {
    margin: auto;
    margin-bottom: 0.2em;
  }
}

header nav ul li .cta {
  width: 100%;
  text-align: left;
}

@media (max-width: 992px) {
  header .portrait a {
    margin: 0;
    max-height: 40vw;
    max-width: 40vw;
  }
}

@media (max-width: 768px) {
  header .row {
    align-items: center;
    text-align: center;
  }
  header .col {
    width: 100%;
  }
  header .portrait a {
    margin: auto;
    max-height: 50vw;
    max-width: 50vw;
  }
  header .text {
    padding-right: 0;
  }
}

@media (max-width: 544px) {
  header .portrait a {
    max-height: 60vw;
    max-width: 60vw;
  }
  header .text {
    margin-top: -1.5em;
    z-index: 1;
  }
  header .text h2 {
    margin-top: -1em;
    font-size: 120%;
  }
}

footer {
  background: linear-gradient(to top, rgba(35, 53, 73, 0.9) 0%, rgba(35, 53, 73, 0.7) 100%);
  bottom: 0;
  font-size: 12px;
  padding: 9px;
  position: fixed;
  text-align: center;
  width: 100%;
}

footer a {
  color: #ffffff;
}

h1,
h2,
h3 {
  font-family: 'Playfair Display', serif;
  font-weight: 400;
}

h1 {
  font-size: 40px;
}

h2 {
  font-size: 32px;
}

h3 {
  font-size: 26px;
}

h4 {
  font-size: 18px;
}

h5 {
  font-size: 14px;
}

h6 {
  font-size: 14px;
  font-weight: 400;
}

section.projects .row {
  align-items: flex-start;
  text-align: center;
}

section.projects .row.project-head {
  padding-top: 6em;
}

section.projects .row:nth-of-type(2) {
  padding-top: 2em;
}

section.projects .row:last-of-type {
  padding-bottom: 2em;
}

section.projects .row .year {
  display: block;
  font-family: inherit;
  font-size: 0.8em;
  opacity: 0.6;
}

section.projects .row hr {
  margin-bottom: 2em;
}

@media (max-width: 768px) {
  section.projects .row hr {
    display: none;
  }
}

section.projects .row .col {
  margin: 0 auto;
}

section.projects .row .col.triple {
  padding-left: 2em;
  text-align: left;
}

@media (max-width: 768px) {
  section.projects .row .col.double {
    margin-bottom: 1.5em;
    width: 100%;
  }
  section.projects .row .col.triple {
    padding-left: 0;
  }
}

section.projects .row .cta {
  margin-top: 1em;
  margin-right: 1em;
}

section.projects .row .project-items {
  display: flex;
  flex-direction: row;
}

section.projects .row .project-items a {
  display: inline-block;
  flex: 1;
  margin: 1em;
}

section.projects .row .project-items a:first-of-type {
  margin-left: 0;
}

section.projects .row .project-items a:last-of-type {
  margin-right: 0;
}

@media (max-width: 768px) {
  section.projects .row .project-items {
    flex-direction: column;
    width: 100%;
  }
  section.projects .row .project-items a {
    display: block;
    flex: 1;
    margin: 0;
    margin-bottom: 1em;
    margin-top: 1em;
  }
}

section.awards .awards-content {
  background: radial-gradient(ellipse at center, rgba(243, 194, 62, 0.25) 0%, rgba(243, 194, 62, 0) 60%);
  background-color: rgba(0, 0, 0, 0.6);
  background-position: top center;
  background-size: 100% 200%;
}

section.awards .awards-content.silver {
  background: radial-gradient(ellipse at center, rgba(255, 255, 255, 0.2) 0%, rgba(255, 255, 255, 0) 60%);
  background-color: rgba(0, 0, 0, 0.6);
  background-position: top center;
  background-size: 100% 200%;
}

section.awards .awards-content.bronze {
  background: radial-gradient(ellipse at center, rgba(216, 107, 74, 0.3) 0%, rgba(216, 107, 74, 0) 60%);
  background-color: rgba(0, 0, 0, 0.6);
  background-position: top center;
  background-size: 100% 200%;
}

section.awards .awards-content.finalist {
  background: radial-gradient(ellipse at center, rgba(65, 150, 246, 0.3) 0%, rgba(65, 150, 246, 0) 60%);
  background-color: rgba(0, 0, 0, 0.6);
  background-position: top center;
  background-size: 100% 200%;
}

section.awards .awards-content.grand-prix {
  background: radial-gradient(ellipse at center, rgba(243, 194, 62, 0.5) 0%, rgba(243, 194, 62, 0) 60%);
  background-color: rgba(0, 0, 0, 0.6);
  background-position: top center;
  background-size: 100% 200%;
}

section.awards h3,
section.awards h4,
section.awards h5 {
  font-weight: 100;
  margin: 0;
  margin-bottom: 0.5em;
  padding: 0;
}

section.awards h3,
section.awards p {
  text-shadow: 0 0 8px #ffffff;
}

section.awards h3 {
  margin-top: 1em;
}

section.awards .year {
  display: block;
  font-family: inherit;
  font-size: 0.8em;
  font-weight: inherit;
  margin-bottom: 0.5em;
  opacity: 0.6;
  text-shadow: none;
}

section.awards .result {
  letter-spacing: 0.5em;
  text-transform: uppercase;
}

section.awards .awards-logo,
section.awards .awards-video {
  margin: -1em;
  padding: 1em;
  position: relative;
}

section.awards .awards-logo a,
section.awards .awards-video a {
  border-radius: 999px;
  display: block;
  overflow: hidden;
  transition: padding ease-in-out 0.05s, margin ease-in-out 0.05s;
}

section.awards .awards-logo a {
  background: radial-gradient(ellipse at center, rgba(255, 255, 255, 0.1) 0%, rgba(255, 255, 255, 0.1) 55%, transparent 55.5%, transparent 100%);
  padding: 2em;
}

@media (max-width: 768px) {
  section.awards .awards-logo a {
    background: radial-gradient(ellipse at center, rgba(255, 255, 255, 0.1) 0%, rgba(255, 255, 255, 0.1) 65%, transparent 65.5%, transparent 100%);
  }
}

@media (max-width: 544px) {
  section.awards .awards-logo a {
    background: radial-gradient(ellipse at center, rgba(255, 255, 255, 0.1) 0%, rgba(255, 255, 255, 0.1) 62%, transparent 62.5%, transparent 100%);
  }
}

section.awards .awards-logo a:hover {
  padding: 1.5em;
}

section.awards .awards-video a {
  margin: 2em;
}

section.awards .awards-video a::after {
  background-image: url("https://www.r5.ro/2020/SpritesTransparent.png");
  background-position: 100% 14.2857%;
  background-repeat: no-repeat;
  background-size: 800% 800%;
  content: '';
  height: 100%;
  left: 30%;
  opacity: 0.6;
  pointer-events: none;
  position: absolute;
  top: 30%;
  transition: opacity ease-out 0.05s, background-size ease-out 0.05s;
  width: 40%;
  height: 40%;
}

section.awards .awards-video a:hover {
  margin: 1.5em;
}

section.awards .awards-video a:hover::after {
  opacity: 0.9;
}

section.awards .col {
  text-align: center;
}

@media (min-width: 769px) and (max-width: 992px) {
  section.awards .col.triple {
    flex: 2;
  }
}

@media (max-width: 768px) {
  section.awards .row {
    align-content: center;
    align-items: center;
  }
  section.awards .col {
    display: inline-block;
    flex: unset;
  }
  section.awards .col.awards-text {
    order: 1;
    width: 100%;
  }
  section.awards .col.awards-logo, section.awards .col.awards-video {
    margin-bottom: 0.5em;
    margin-top: -2em;
    max-width: 50vw;
    order: 2;
    width: 50%;
  }
  section.awards .awards-logo a {
    padding: 1em;
  }
  section.awards .awards-logo a:hover {
    padding: unset;
  }
  section.awards .awards-text {
    padding: 0 1em;
  }
  section.awards .awards-video a {
    margin: 1em;
  }
  section.awards .awards-video a:hover {
    margin: unset;
  }
}
